import { CircularProgress, Grid, IconButton, Typography } from "@mui/material";
import React from "react";
import { Api } from "../core/api";
import useApi from "../hooks/useApi";
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';
import RemoveOutlinedIcon from '@mui/icons-material/RemoveOutlined';

export function Balance() {
    const { request: userInfoRequest, data: userInfo, isLoading: isUserInfoLoading } = useApi(Api.getUserInfo, { callOnInit: true });
    const { request: addCreditRequest, data: sad, isLoading: isAddCreditLoading } = useApi(Api.addCredit);
    const { request: withdrawCreditRequest, data: sadad, isLoading: isWithdrawCreditLoading } = useApi(Api.addCredit);

    const handleBalanceButton = (req) => {
        req({ user_uuid: userInfo.user_uuid, amount: 9999.1 }).then(() => {
            userInfoRequest();
        })
    }

    const Balance = (
        <>
            <Grid container spacing={2} sx={{ pb: 0 }} alignItems="center">
                <Grid item>
                    <IconButton disabled={isUserInfoLoading} onClick={() => handleBalanceButton(withdrawCreditRequest)} color="error"> 
                        {
                            isWithdrawCreditLoading ?
                                <CircularProgress color="warning" size={15} /> :
                                <RemoveOutlinedIcon />
                        }
                     </IconButton>
                </Grid>
                <Grid item>
                    <Typography variant="h5" textAlign="center" sx={{ mb: 0 }}>
                        {
                            isUserInfoLoading ?
                                <CircularProgress color="warning" size={15} /> :
                                Math.round(userInfo.balance * 1000) / 1000
                        }
                    </Typography>
                </Grid>
                <Grid item>
                    <IconButton disabled={isUserInfoLoading} onClick={() => handleBalanceButton(addCreditRequest)} color="success">
                        {
                            isAddCreditLoading ?
                                <CircularProgress color="warning" size={15} /> :
                                <AddOutlinedIcon /> 
                        }
                    </IconButton>
                </Grid>
            </Grid>
        </>
    )

    return (
        <Grid container spacing={2} sx={{ pb: 3 }} justifyContent="center" alignItems="center">
            <Grid item>
                <Typography variant="h6" textAlign="center" sx={{ mb: 0 }}>
                    Available balance:
                </Typography>
            </Grid>
            <Grid item>
                {Balance}
            </Grid>
        </Grid>
    )
}