import ky from "../helpers/ky";
import { API_HOST, API_PROTOCOL } from "./constants";

const requestClient = ky.extend({
    prefixUrl: `${API_PROTOCOL}://${API_HOST}`,
    hooks: {
		beforeRequest: [
			request => {
                const token = localStorage.getItem('auth_token');
				token && !request.headers.get('Authorization') && request.headers.set('Authorization', 'Bearer ' + token);
			}
		]
	}
})

class ApiClient {
    signIn(credentinals: { username: string, password: string }) {
        return () => requestClient.post(
            'auth',
             { 
                 json: { email: credentinals.username, password: credentinals.password },
                 headers: {
                     'Content-type': 'text/plain'
                 }
             }).text()
    }

    getOptionsList(data?: any) {
        return () => requestClient.post('list_options', {
            json: data,
            headers: {
                'Content-type': 'application/json'
            }
        }).json()
    }

    addOption(data: any) {
        return () => requestClient.post('create_option', { json: data }).text()
    }

    closeOption(uuid: any) {
        return () => requestClient.post('close_option', { json: { uuid } }).json()
    }

    getUserInfo() {
        return () => requestClient.get('user_info', {
            headers: {
                'Content-type': 'application/json'
            }
        }).json()
    }

    addCredit(data: any) {
        return () => requestClient.post('add_credit', { json: data, headers: { Authorization: 'Bearer 1111' } }).json()
    }

    withdrawCredit(data: any) {
        return () => requestClient.post('withdraw_credit', { json: data, headers: { Authorization: 'Bearer 1111' } }).json()
    }
}

const Api = new ApiClient();

export { Api }